:before, :after {
  box-sizing: border-box;
}

body {
  color: #2a2a2a;
  font-family: Open Sans, -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, sans-serif;
  line-height: 1.5;
}

.search-form {
  width: 600px;
  margin: 0 auto 40px;
  display: flex;
}

.search-form input {
  height: 40px;
  border: 3px solid #2ecc71;
  border-radius: 30px;
  flex-grow: 1;
  padding-left: 30px;
  font-size: 18px;
}

.search-form button {
  color: #fff;
  text-transform: uppercase;
  cursor: pointer;
  background-color: #2ecc71;
  border: none;
  border-radius: 10px;
  padding: 10px 20px;
  font-size: 14px;
}

.load-more {
  color: #fff;
  text-transform: uppercase;
  cursor: pointer;
  background-color: #2ecc71;
  border: none;
  border-radius: 10px;
  margin: 0 auto;
  padding: 10px 20px;
  font-size: 14px;
  transition: background-color .25s linear;
  display: block;
}

.load-more.is-hidden {
  display: none;
}

.search-btn:hover, .search-btn:focus, .load-more:hover, .load-more:focus {
  background-color: #27ae60;
}

.gallery {
  grid-gap: 20px;
  max-width: calc(100vw - 80px);
  flex-wrap: wrap;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  margin-bottom: 20px;
  margin-left: auto;
  margin-right: auto;
  padding-top: 50px;
  display: grid;
}

img {
  width: 100%;
  height: 200px;
  object-fit: cover;
  display: block;
}

